.page__body {
  color: #4f4f4d;
  font-size: 17px;
  line-height: 1.52941;
  font-family: "Roboto", sans-serif;
  background-color: #ffffff;
}

.page__inner {
  display: flex;
  flex-flow: column;
  min-width: 320px;
  height: 2000px;
  min-height: 100vh;
  padding-top: 130px;
  overflow: hidden;
  background: #444;
}

@media (max-width: 1229.98px) {
  .page .page__inner {
    padding-top: 70px;
  }
}

@media (max-width: 899.98px) {
  .page--open-panel {
    overflow: hidden;
  }
  .page--open-panel .header__toggle-panel svg:nth-child(1) {
    display: none;
  }
  .page--open-panel .header__toggle-panel svg:nth-child(2) {
    display: block;
  }
  .page--open-panel .header__panel {
    transform: translateX(0);
  }
}

.header {
  height: 130px;
  padding: 30px 0;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1030;
  background-color: #ffffff;
}

.header__logo {
  color: #181716;
  font-size: 70px;
  text-decoration: none;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.header__logo-img {
  display: block;
}

.header__lang-inner {
  display: flex;
  margin: 0 -7.5px;
}

.header__lang-item {
  color: #4f4f4d;
  font-size: 20px;
  line-height: 1.25;
  font-family: "DIN Pro", sans-serif;
  text-decoration: none;
  margin: 0 7.5px;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.header__lang-item:hover, .header__lang-item--current {
  color: #fb953b;
}

.header__log-in {
  text-decoration: none;
  display: flex;
  align-items: center;
}

.header__log-in:hover .header__log-in-icon::before {
  opacity: 0;
}

.header__log-in:hover .header__log-in-icon::after {
  opacity: 1;
}

.header__log-in-icon {
  width: 70px;
  height: 70px;
  flex-shrink: 0;
  position: relative;
}

.header__log-in-icon::before, .header__log-in-icon::after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transition: opacity 0.15s linear;
}

