.header__log-in-title {
  color: #4f4f4d;
  font-size: 20px;
  line-height: 1.25;
  font-family: "DIN Pro", sans-serif;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 150px;
  order: -1;
  margin-right: 15px;
}

.header__toggle-panel {
  color: #4f4f4d;
  font-size: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  padding: 0;
  border: 0;
  margin-left: auto;
  background: transparent;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.header__toggle-panel svg:nth-child(2) {
  display: none;
}

.header--fixed {
  height: 70px;
  padding: 15px 0;
  position: fixed;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.1);
}

.header--fixed .header__logo {
  font-size: 40px;
}

.header--fixed .header__log-in-icon {
  width: 40px;
  height: 40px;
}

@media (min-width: 900px) {
  .header--transparent:not(.header--fixed) {
    background-color: transparent;
  }
  .header--transparent:not(.header--fixed) .header__logo {
    color: white;
  }
  .header--transparent:not(.header--fixed) .header__toggle-panel {
    color: white;
  }
  .header--transparent:not(.header--fixed) .menu__link {
    color: white;
  }
  .header--transparent:not(.header--fixed) .menu__link:hover, .header--transparent:not(.header--fixed) .menu__link--current {
    color: #fb953b;
  }
  .header--transparent:not(.header--fixed) .header__lang-item {
    color: white;
  }
  .header--transparent:not(.header--fixed) .header__lang-item:hover, .header--transparent:not(.header--fixed) .header__lang-item--current {
    color: #fb953b;
  }
  .header--transparent:not(.header--fixed) .header__log-in-title {
    color: white;
  }
}

@media (max-width: 899.98px) {
  .page:not(.page--open-panel) .header--transparent:not(.header--fixed) {
    background-color: transparent;
  }
  .page:not(.page--open-panel) .header--transparent:not(.header--fixed) .header__logo {
    color: white;
  }
  .page:not(.page--open-panel) .header--transparent:not(.header--fixed) .header__toggle-panel {
    color: white;
  }
  .page:not(.page--open-panel) .header--transparent:not(.header--fixed) .menu__link {
    color: white;
  }
  .page:not(.page--open-panel) .header--transparent:not(.header--fixed) .menu__link:hover, .page:not(.page--open-panel) .header--transparent:not(.header--fixed) .menu__link--current {
    color: #fb953b;
  }
  .page:not(.page--open-panel) .header--transparent:not(.header--fixed) .header__lang-item {
    color: white;
  }
  .page:not(.page--open-panel) .header--transparent:not(.header--fixed) .header__lang-item:hover, .page:not(.page--open-panel) .header--transparent:not(.header--fixed) .header__lang-item--current {
    color: #fb953b;
  }
  .page:not(.page--open-panel) .header--transparent:not(.header--fixed) .header__log-in-title {
    color: white;
  }
}

@media (max-width: 1229.98px) {
  .header {
    height: 70px;
    padding: 15px 0;
  }
  .header .header__logo {
    font-size: 40px;
  }
  .header .header__lang-item {
    font-size: 16px;
  }
  .header .header__log-in-icon {
    width: 40px;
    height: 40px;
  }
  .header .header__log-in-title {
    font-size: 16px;
  }
}

@media (max-width: 899.98px) {
  .header__panel {
    display: flex;
    flex-flow: column;
    width: 280px;
    position: fixed;
    top: 70px;
    right: 0;
    bottom: 0;
    background-color: #ffffff;
    box-shadow: -10px 0 8px -8px rgba(0, 0, 0, 0.1), inset 0 12px 8px -8px rgba(0, 0, 0, 0.1);
    transform: translateX(100%);
    transition: transform 0.15s ease-out;
  }
  .header__panel-header {
    height: 70px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  }
  .header__panel-content {
    flex-grow: 1;
    overflow-y: auto;
  }
  .header__panel-row {
    margin: 0;
  }
  .header__panel-col {
    padding: 15px 25px;
  }
  .header__panel-col + .header__panel-col {
    border-top: 1px solid rgba(0, 0, 0, 0.1);
  }
  .header__menu {
    margin: 0 -25px;
  }
  .header__log-in-title {
    order: 0;
    max-width: none;
    margin: 0 0 0 15px;
  }
}

.menu {
  overflow: hidden;
}

.menu__inner {
  list-style: none;
  display: flex;
  padding: 0;
  margin: 0 -35px;
}

.menu__item {
  overflow: hidden;
  margin: 0 35px;
}

.menu__link {
  color: #4f4f4d;
  font-size: 20px;
  line-height: 1.25;
  font-family: "DIN Pro", sans-serif;
  text-decoration: none;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: block;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.menu__link:hover, .menu__link--current {
  color: #fb953b;
}

@media (max-width: 1229.98px) {
  .menu__inner {
    margin: 0 -20px;
  }
  .menu__item {
    margin: 0 20px;
  }
  .menu__link {
    font-size: 16px;
  }
}

@media (max-width: 899.98px) {
  .menu__inner {
    flex-flow: column;
    margin: 0;
  }
  .menu__item {
    margin: 0;
  }
  .menu__link {
    padding: 7.5px 25px;
  }
}
